<template>
  <div>
        <Menu></Menu>
    <v-container>
      <v-layout>
        <v-flex>
          <v-card>
            <v-col>
              <v-text-field
                label="Codigo"
                v-on:keyup.enter="setFocus"
                v-model="code"
              ></v-text-field>
              <v-text-field
                label="Cantidad"
                v-on:keyup.enter="getProductById"
                ref="search"
                v-model="quantity"
              ></v-text-field>

              <div class="text-center pb-5">
                <v-btn rounded color="#000000" dark @click="getProductById"
                  >Agregar</v-btn
                >
              </div>
              <div class="text-center">
                <v-layout>
                  <v-flex>
                    <v-btn rounded color="#000000" dark @click="addDialogConfirmation"
                      >Devolver</v-btn
                    >
                  </v-flex>
                  <v-flex>
                    <v-btn rounded color="#000000" dark @click="limpiarVenta"
                      >Cancelar</v-btn
                    >
                  </v-flex>
                </v-layout>
              </div>
            </v-col>

            <v-alert close-label type="error" v-model="alert"
              >Debe ingresar todos los campos, o bien el cdigo es
              invlido</v-alert
            >
            <v-alert type="error" v-model="errorRequest"
              >Cdigo de producto no encontrado</v-alert
            >
            <v-alert type="error" v-model="errorRequest2"
              >No ha agregado productos para la devolucion</v-alert
            >
          </v-card>
        </v-flex>

        <v-col cols="12" md="9">
          <v-data-table
            :headers="headers"
            :items="products"
            @click:row="clickElement"
            :items-per-page="100"
            item-key="id"
            class="elevation-7"
          ></v-data-table>

          <div class="text-center">
            <v-sheet color="orange lighten-2">{{ totalVenta }}</v-sheet>
          </div>
        </v-col>
      </v-layout>
      <!-- Notificaciones-->

      <v-dialog v-model="dialogConfirmation" width="600">
        <v-card>
          <v-card-title class="justify-center"
            >Confirma la devolucion?</v-card-title
          >
          <v-container>
            <div class="text-center pt-12">
              <v-layout>
                <v-flex>
                  <v-btn rounded color="#000000" dark @click="generarVenta"
                    >Devolver</v-btn
                  >
                </v-flex>
                <v-flex>
                  <v-btn
                    class=""
                    rounded
                    color="#000000"
                    dark
                    @click="dialogConfirmation = false"
                    >Cancelar</v-btn
                  >
                </v-flex>
              </v-layout>
            </div>
          </v-container>
        </v-card>
      </v-dialog>
      <v-snackbar v-model="snackbar">
        {{ text }}
        <v-btn color="pink" text @click="snackbar = false">Close</v-btn>
      </v-snackbar>
    </v-container>
  </div>
</template>
<script>
import axios from "axios";
import Menu from "../components/Menu.vue";
export default {
    components: { Menu },
  data() {
    return {
      snackbar: false,
      text: "Devolucin realizada con exito",
      code: "",
      alert: false,
      errorRequest: false,
      errorRequest2: false,
      dialogConfirmation: false,
      quantity: 1,
      product: null,
      products: [],
      headers: [
        //{ text: "Id", value: "id" },
        { text: "Nombre", value: "name" },
        { text: "Precio", value: "price" },
        { text: "Cantidad", value: "quantity" },
        { text: "Total", value: "total" },
      ],
    };
  },
  methods: {
    clickElement() {
      this.products.forEach((product) => {
        console.log(product);
      });
    },
    setFocus() {
      // Note, you need to add a ref="search" attribute to your input.
      this.$refs.search.focus();
    },
    addDialogConfirmation() {

      if(this.products.length <= 0){
        this.errorRequest2 = true;
      }else{
      this.errorRequest2 = false;
      this.dialogConfirmation = true;
      }

    },
    generarVenta() {
      if (this.products.length > 0) {
        let auxProducts = {
          products: [], //este es el parametro que espera el back
        };

        this.products.forEach((element) => {
          let auxObject = {
            product_id: element.id,
            quantity: element.quantity,
            price: element.price,
          };

          auxProducts.products.push(auxObject);
        });

        console.warn(auxProducts);

        axios
          .post("http://127.0.0.1:8000/api/devolution/", auxProducts)
          .then(this.limpiarVenta())
          .then((this.snackbar = true));
      } else {
        //TODO: que notifique cuando no hay productos en la venta.
      }
    },
    getProductById(code) {
      if (this.code.length > 0 && this.quantity > 0) {
        this.alert = false;
        this.errorRequest = false;
        axios
          .get("http://127.0.0.1:8000/api/searchcode/" + this.code)
          .then((response) => this.formatearProducto(response.data))
          .then((this.code = ""))
          .catch((error) => (this.errorRequest = true));
      } else {
        this.alert = true;
      }
    },
    limpiarVenta() {
      this.products = [];
      this.code = "";
      this.quantity = 1;
    },
    formatearProducto(producto) {
      let quantity;

      const encontrado = this.products.find((p) => p.id === producto.id);
      let index = null;
      //    console.log(encontrado);
      if (encontrado != null) {
        quantity = parseInt(encontrado.quantity) + parseInt(this.quantity);
        index = this.products.indexOf(encontrado); //posicion del array en donde est el producto encontrado
        //  console.log(index);
      } else {
        quantity = this.quantity;
      }

      const formateado = {
        id: producto.id,
        name: producto.name,
        code: producto.code,
        price: producto.price,
        quantity: quantity,
        total: producto.price * quantity,
      };

      if (index != null) {
        this.products.splice(index, 1);

        this.products.push(formateado);
        //  console.log(this.products)
      } else {
        this.products.push(formateado);
      }
    },
  },
  computed: {
    totalVenta() {
      let auxPrice = 0;
      let iva = 0;

      if (this.products.length > 0) {
        this.products.forEach((element) => {
          auxPrice = auxPrice + element.price * element.quantity;
        });

        iva = auxPrice * 0.19;
        auxPrice = auxPrice + iva;

        return "el total de la devolucin es " + auxPrice;
      }
    },
  },
};
</script>
