<template>
  <div>
    <Menu></Menu>
    <v-container fluid>
      <v-layout>
        <v-layout>
          <!---------------------------------------------------------------------------------->
          <v-flex class="pt-4">
            <v-card>
              <v-card-title> Ingreso de productos </v-card-title>
              <v-col>
                <v-text-field
                  label="Codigo"
                  v-on:keyup.enter="setFocus"
                  v-model="code"
                ></v-text-field>

                <v-text-field
                  label="Cantidad"
                  v-on:keyup.enter="getProductById"
                  ref="search"
                  v-model="quantity"
                ></v-text-field>

                <div class="text-center pt-4">
                  <v-btn rounded color="#000000" dark @click="getProductById"
                    >Agregar a la venta</v-btn
                  >
                </div>

                <v-layout>
                  <v-flex>
                    <v-col cols="12">
                      <div class="text-center pt-12">
                        <h1 style="color: black">Bruto: ${{ totalBruto }}</h1>
                        <h1 style="color: black">Iva: ${{ totalIva }}</h1>
                        <h1 style="color: black">Total: ${{ totalNeto }}</h1>
                      </div>
                    </v-col>

                    <v-select
                      v-model="PaymentMethodSelected"
                      :items="paymentMethods"
                      label="Seleccione el metodo de pago"
                      data-vv-name
                      item-value="id"
                      item-text="name"
                      required
                    ></v-select>

                    <div class="text-center pt-12">
                      <v-layout>
                        <v-flex>
                          <v-btn
                            rounded
                            color="#000000"
                            dark
                            @click="sellConfirmation"
                            >Vender</v-btn
                          >
                        </v-flex>
                        <v-flex>
                          <v-btn
                            class=""
                            rounded
                            color="#000000"
                            dark
                            @click="limpiarVenta"
                            >Cancelar</v-btn
                          >
                        </v-flex>
                      </v-layout>
                    </div>
                  </v-flex>
                </v-layout>
              </v-col>
              <v-alert type="error" v-model="alert"
                >El codigo que ha ingresado es invalido o la cantidad de
                productos es menor a 0</v-alert
              >
              <v-alert type="error" v-model="alert2"
                >No se ha seleccionado un metodo de pago</v-alert
              >
              <v-alert type="error" v-model="alert3"
                >No se han ingresado productos a la venta</v-alert
              >
              <v-alert type="error" v-model="errorRequest"
                >Cdigo de producto no encontrado</v-alert
              >
            </v-card>
          </v-flex>
          <!------------------------------------------------------------------------------------->
          <v-dialog v-model="dialogAdd" width="600">
            <v-card>
              <v-card-title class="justify-center"
                >Confirma la venta?</v-card-title
              >
              <v-container>
                <div class="text-center pt-12">
                  <v-layout>
                    <v-flex>
                      <v-btn rounded color="#000000" dark @click="generarVenta"
                        >Vender</v-btn
                      >
                    </v-flex>
                    <v-flex>
                      <v-btn
                        class=""
                        rounded
                        color="#000000"
                        dark
                        @click="dialogAdd = false"
                        >Cancelar</v-btn
                      >
                    </v-flex>
                  </v-layout>
                </div>
              </v-container>
            </v-card>
          </v-dialog>

          <v-col cols="12" md="9">
            <v-data-table
              :headers="headers"
              :items="products"
              @click:row="clickElement"
              :items-per-page="100"
              item-key="id"
              class="elevation-7"
            ></v-data-table>
          </v-col>
        </v-layout>
      </v-layout>

      <!-- Notificaciones-->
      <v-snackbar v-model="snackbar">
        {{ text }}
        <v-btn color="pink" text @click="snackbar = false">Close</v-btn>
      </v-snackbar>
    </v-container>
  </div>
</template>


<script>
import axios from "axios";
import Menu from "../components/Menu.vue";
export default {
  components: { Menu },
  data() {
    return {
      snackbar: false,
      text: "Venta realizada con exito",
      code: "",
      alert: false,
      errorRequest: false,
      quantity: 1,
      product: null,
      alert2: false,
      alert3: false,
      products: [],
      dialogAdd: false,
      paymentMethods: [],
      PaymentMethodSelected: null,
      headers: [
        //{ text: "Id", value: "id" },
        { text: "Nombre", value: "name" },
        { text: "Precio", value: "price" },
        { text: "Cantidad", value: "quantity" },
        { text: "Total", value: "total" },
      ],
    };
  },

  mounted: function () {
    this.getPaymentMethod();
  },

  methods: {
    clickElement() {
      this.products.forEach((product) => {
        console.log(product);
      });
    },
    getPaymentMethod() {
      axios
        .get("http://127.0.0.1:8000/api/PaymentMethod/")
        .then((response) => (this.paymentMethods = response.data));
      console.warn(this.paymentMethods);
    },
    setFocus() {
      // Note, you need to add a ref="search" attribute to your input.
      this.$refs.search.focus();
    },
    generarVenta() {
      if (this.products.length > 0) {
        let auxProducts = {
          products: [], //este es el parametro que espera el back
          payment: this.PaymentMethodSelected,
        };

        let permitirTransaccion = true;
        let productCritical = false;

        this.products.forEach((element) => {
          if (element.stock < element.quantity) {
            permitirTransaccion = false;
            this.text =
              "El producto " + element.name + " no tiene suficiente stock";
            this.snackbar = true;
          } else {
            if (element.stock - element.quantity < element.critical) {
              productCritical = true;
            }
            this.text = "Venta realizada con exito";
            this.dialogAdd = false;
          }

          console.log("productos: ", element);

          let auxObject = {
            product_id: element.id,
            quantity: element.quantity,
            price: element.price,
          };

          // console.log(this.PaymentMethodSelected)
          auxProducts.products.push(auxObject);
        });

        console.warn(auxProducts);
        if (permitirTransaccion) {
          axios
            .post("http://127.0.0.1:8000/api/sell/", auxProducts)
            .then(this.limpiarVenta())
            .then((this.snackbar = true));
        }
        if (productCritical) {
          alert("Un producto esta en stock critico");
        }
      } else {
        //TODO: que notifique cuando no hay productos en la venta.
      }
    },

    sellConfirmation(code) {
      if (this.PaymentMethodSelected == null) {
        this.alert2 = true;
      } else {
        this.alert2 = false;
        if (this.products.length <= 0) {
          this.alert3 = true;
        } else {
          this.alert3 = false;
          this.dialogAdd = true;
        }
      }

      // this.alert2 = false;
    },
    getProductById(code) {
      if (this.code.length > 0 && this.quantity > 0) {
        this.alert = false;
        this.errorRequest = false;
        axios
          .get("http://127.0.0.1:8000/api/searchcode/" + this.code)
          .then((response) => this.formatearProducto(response.data))
          .then((this.code = ""))
          .catch((error) => (this.errorRequest = true));
      } else {
        this.alert = true;
      }
    },
    limpiarVenta() {
      this.products = [];
      this.code = "";
      this.quantity = 1;
    },
    formatearProducto(producto) {
      let quantity;

      const encontrado = this.products.find((p) => p.id === producto.id);
      let index = null;
      //    console.log(encontrado);
      if (encontrado != null) {
        quantity = parseInt(encontrado.quantity) + parseInt(this.quantity);
        index = this.products.indexOf(encontrado); //posicion del array en donde est el producto encontrado
        //  console.log(index);
      } else {
        quantity = this.quantity;
      }

      const formateado = {
        id: producto.id,
        name: producto.name,
        code: producto.code,
        price: producto.price,
        stock: producto.stock,
        critical: producto.critical,
        quantity: quantity,
        total: producto.price * quantity,
      };

      if (index != null) {
        this.products.splice(index, 1);

        this.products.push(formateado);
        //  console.log(this.products)
      } else {
        this.products.push(formateado);
      }
    },
  },
  computed: {
    totalBruto() {
      let neto = 0;
      let iva = 0;
      let bruto = 0;

      if (this.products.length > 0) {
        this.products.forEach((element) => {
          bruto = bruto + element.price * element.quantity;

          iva = iva + element.price * element.quantity * 0.19;

          neto = bruto + iva;
        });
        return Math.round(bruto);
      }
    },
    totalIva() {
      let neto = 0;
      let iva = 0;
      let bruto = 0;

      if (this.products.length > 0) {
        this.products.forEach((element) => {
          iva = iva + element.price * element.quantity * 0.19;
        });
        return Math.round(iva);
      }
    },
    totalNeto() {
      let neto = 0;
      let iva = 0;
      let bruto = 0;

      if (this.products.length > 0) {
        this.products.forEach((element) => {
          bruto = bruto + element.price * element.quantity;

          iva = iva + element.price * element.quantity * 0.19;

          neto = bruto + iva;
        });
        return Math.round(neto);
      }
    },
  },
};
</script>
