<template>

  <div>
        <Menu></Menu>
    <v-container>
      <v-layout>
        <v-flex>
          <template v-slot:activator="{ on }">
            <v-text-field
              v-model="date"
              label="Selecciona una fecha"
              prepend-icon="event"
              readonly
              v-on="on"
            ></v-text-field>
          </template>

          <v-date-picker
            v-model="date"
            v-on:change="GetData"
            no-title
            scrollable
            locale="es-Es"
          >
          </v-date-picker>
          
          <v-col class="text-center" cols="12" md="9">
            <v-card color="#000000">
              <h3 style="color: white">Efectivo:</h3>
              <v-sheet
                color="#964D08"
            
                v-model="totalView"
              >
                <h3 style="color: white">$ {{ efectivo }}</h3>
              </v-sheet>

              <h3 style="color: white">TDC/TDD:</h3>
              <v-sheet
                color="#964D08"
              
                v-model="totalView"
              >
                <h3 style="color: white">$ {{ TDCD }}</h3>
              </v-sheet>

              <h3 style="color: white">T. Bancaria:</h3>
              <v-sheet
                color="#964D08"

                v-model="totalView"
              >
                <h3 style="color: white">$ {{ Tbancaria }}</h3>
              </v-sheet>

              <h3 style="color: white">Devoluciones:</h3>
              <v-sheet
                color="#964D08"
            
                v-model="totalView"
              >
                <h3 style="color: white">$ {{ devolucionMethod}}</h3>
              </v-sheet>

              <h3 style="color: white">Total del dia:</h3>
              <v-sheet
                color="#964D08"
                v-model="totalView"
              >
                <h3 style="color: white">$ {{ total }}</h3>
              </v-sheet>
            </v-card>

            <!-- <v-alert type="info" v-model="totalNull" color="#000000"
                  >Da sin transacciones</v-alert
                > -->
          </v-col>
        </v-flex>
        <!-------------->
        <v-flex> </v-flex>
        <!-------------->
        <v-col cols="12" md="9">
          <v-data-table
            :headers="headers"
            :items="products"
            :items-per-page="10"
            item-key="a+1"
            group-by="sell_id"
            class="elevation-7"
          >
            <template v-slot:group.header="{ items, isOpen, toggle }">
              <td :colspan="headers.length">
                <v-icon @click="toggle">
                  {{ isOpen ? "mdi-minus" : "mdi-plus" }}
                </v-icon>
                <h3>Metodo de pago:{{ payment_method.name }}</h3>
              </td>
            </template>
          </v-data-table>
        </v-col>
      </v-layout>
    </v-container>
  </div>
</template>


<script>

import axios from "axios";
import Menu from "../components/Menu.vue";
import Vue from "vue";

export default {
    components: { Menu },
  data() {
    return {
      count: 0,
      products: [],
      sell_id: [],
      quantity: [],
      producto: "",
      payment_method: [],
      total: 0,
      efectivo: 0,
      TDCD: 0,
      Tbancaria: 0,
      devolucionMethod: 0,
      totalNull: false,
      date: new Date().toISOString().substr(0, 9),
      menu: false,
      totalView: true,
      noBalanceAlert: false,
      headers: [
        { text: "Codigo", value: "code" },
        { text: "Nombre", value: "name" },
        { text: "Cantidad", value: "quantity" },
        { text: "Precio", value: "price" },
        { text: "Fecha", value: "created_at" },
      ],
    };
  },
  mounted: function () {
    this.getSellsByDate();
    this.getTotal();
    this.getQuantityByDate();
  },
  methods: {
    GetData(date) {
      this.getTotal(date);
      this.getEfectivo(date);
      this.getCredito(date);
      this.getBank(date);
      this.getSellsByDate(date);
      this.getQuantityByDate(date);
      this.getDevolution(date);
    },

    getTotal(date) {
      // console.warn(date);
      axios
        .get("http://127.0.0.1:8000/api/daily/" + date)
        .then((response) => (this.total = response.data))
        .catch((error) => this.getTotalNull());
    },
    //--------------------------------------------------------------------------------------------------
    //--------------------------------------------------------------------------------------------------
    getEfectivo(date) {
      // console.warn(date);
      axios
        .get("http://127.0.0.1:8000/api/cash/" + date)
        .then((response) => (this.efectivo = response.data))
        .catch((error) => {
          this.errorRequest = true;
          this.efectivo = 0;
          console.log("try catch error");
        });
    },

    getCredito(date) {
      // console.warn(date);
      axios
        .get("http://127.0.0.1:8000/api/tdcd/" + date)
        .then((response) => (this.TDCD = response.data))
        .catch((error) => {
          this.errorRequest = true;
          this.TDCD = 0;
          console.log("try catch error");
        });
    },

    getBank(date) {
      // console.warn(date);
      axios
        .get("http://127.0.0.1:8000/api/tbank/" + date)
        .then((response) => (this.Tbancaria = response.data))
        .catch((error) => {
          this.errorRequest = true;
          this.Tbancaria = 0;
          console.log("try catch error");
        });
    },

    getDevolution(date) {
      // console.warn(date);
      axios
        .get("http://127.0.0.1:8000/api/dev/" + date)
        .then((response) => (this.devolucionMethod = response.data))
        .catch((error) => {
          this.errorRequest = true;
          this.devolucion = 0;
          console.log("try catch error");
        });
    },
    //--------------------------------------------------------------------------------------------------
    //--------------------------------------------------------------------------------------------------
    getQuantityByDate(date) {
      // console.warn(date);
      axios
        .get("http://127.0.0.1:8000/api/searchQuantity/" + date)
        .then((response) => {
          // console.log(response.data);
          //  this.quantitypush(response.data);
        })
        .catch((error) => this.getTotalNull());
    },

    getSellsByDate(date) {
      this.products = [];
      //console.warn(date);
      axios
        .get("http://127.0.0.1:8000/api/searchdate/" + date)
        .then((response) => {
          // console.log(response.data);
          response.data.forEach((sell, index) => {
            console.warn(index);

            this.products.push(sell);
            this.getProductById(sell.product_id, index);
            this.getSellById(sell.sell_id, index);
            this.quantity.push(sell.quantity);
            this.sell_id.push(sell.id);
          });
        })
        .catch((error) => {
          this.errorRequest = true;
          console.log(error);
        });
    },

    getProductById(id, index) {
      // console.log("id: " + id);

      axios
        .get("http://127.0.0.1:8000/api/searchid/" + id)
        .then((response) => {
          // console.log("blablabla",response.data)
          // this.products.push(response.data);
          //this.products[index].name=response.data.name;
          // console.log(response.data.name);
          Vue.set(this.products[index], "name", response.data.name);
          Vue.set(this.products[index], "code", response.data.code);
        })
        .catch((error) => {
          this.errorRequest = true;
          console.log(error);
        });
    },

    getSellById(id) {
      // console.log("sell id: " + id);

      axios
        .get("http://127.0.0.1:8000/api/searchsellid/" + id)
        .then((response) => {
          response.data.forEach((sell, index) => {
            //  console.warn(sell, index);

            this.getMethodById(sell.payment_method_id, index);
            console.log(sell.payment_method_id);
          });

          // console.log(response.data);
        })
        .catch((error) => {
          this.errorRequest = true;
          console.log("try catch error");
        });
    },

    getMethodById(id, index) {
      // console.log("sell id: " + index);

      axios
        .get("http://127.0.0.1:8000/api/searchmethodid/" + id)
        .then((response) => (this.payment_method = response.data))
        .catch((error) => {
          this.errorRequest = true;
          console.log("try catch error");
        });
    },

    uploadDate(data) {
      this.totalNull = false;
      this.getTotal();
      this.getSellsByDate();
    },
    getTotalNull() {
      this.total = 0;

      this.totalNull = true;
    },
    formatearProducto(producto) {
      // console.log(producto);
      let quantity;

      const encontrado = this.products.find((p) => p.id === producto.id);
      let index = null;
      //    console.log(encontrado);
      if (encontrado != null) {
        quantity = parseInt(encontrado.quantity) + parseInt(this.quantity);
        index = this.products.indexOf(encontrado); //posicion del array en donde est el producto encontrado
        //  console.log(index);
      } else {
        quantity = this.quantity;
      }

      const formateado = {
        id: producto.id,
        name: producto.name,
        code: producto.code,
        price: producto.price,
        quantity: quantity,
        total: producto.price * quantity,
      };

      if (index != null) {
        this.products.splice(index, 1);

        this.products.push(formateado);
        //  console.log(this.products)
      } else {
        this.products.push(formateado);
      }
    },
  },
  computed: {},
};
</script>
