<template>
  <div class="login">
    <h1 class="title">Inicio de sesion</h1>
    <form action class="form" @submit.prevent="login">
      <label class="form-label" for="#email">Email:</label>
      <input
        v-model="email"
        class="form-input"
        type="email"
        id="email"
        required
        placeholder="Email"
      />
      <label class="form-label" for="#password">Contrasea:</label>
      <input
        v-model="password"
        class="form-input"
        type="password"
        id="password"
        placeholder="Contrasea"
      />
      <p v-if="error" class="error">
        Has introducido mal el email o la contrasea.
      </p>

      <input class="form-submit" type="submit" value="Iniciar" />

      <input
        class="form-submit"
        type="button"
        value="Registrarse"
        @click="sellConfirmation"
      />
    </form>

    <v-dialog v-model="dialogRegister" width="600">
      <v-card>
        <v-card-title class="justify-center"></v-card-title>

        <div class="text-center pt-12">
          <v-layout>
            <form action class="form" @submit.prevent="register">
              <label class="form-label" for="#email">Email:</label>
              <input
                v-model="email"
                class="form-input"
                type="email"
                id="email"
                required
                placeholder="Email"
              />
              <label class="form-label" for="#password">Contrasea:</label>
              <input
                v-model="password"
                class="form-input"
                type="password"
                id="password"
                placeholder="Contrasea"
              />
              <label class="form-label" for="#password-repeat"
                >Repite la contraea:</label
              >
              <input
                v-model="passwordRepeat"
                class="form-input"
                type="password"
                id="password-repeat"
                placeholder="Contrasea"
              />
              <input class="form-submit" type="submit" value="Registrarme" />
            </form>
          </v-layout>
        </div>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  data: () => ({
    email: "",
    password: "",
    passwordRepeat: "",
    error: false,
    dialogRegister: false,
  }),
  methods: {
    login(email,password) {

        axios
          .get("http://127.0.0.1:8000/api/searchcode/" + this.code + this.password)
          .then((response) => this.formatearProducto(response.data))
          .then((this.code = ""))
          .catch((error) => (this.errorRequest = true));

    },
    async register() {
      try {
        await auth.register(this.email, this.password);
        this.$router.push("/");
      } catch (error) {
        console.log(error);
      }
    },

    sellConfirmation() {
      this.dialogRegister = true;
    },
  },
};
</script>

<style lang="scss" scoped>
.login {
  padding: 2rem;
}
.title {
  text-align: center;
}
.form {
  margin: 3rem auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 20%;
  min-width: 350px;
  max-width: 100%;
  background: rgba(19, 35, 47, 0.9);
  border-radius: 5px;
  padding: 40px;
  box-shadow: 0 4px 10px 4px rgba(0, 0, 0, 0.3);
}
.form-label {
  margin-top: 2rem;
  color: white;
  margin-bottom: 0.5rem;
  &:first-of-type {
    margin-top: 0rem;
  }
}
.form-input {
  padding: 10px 15px;
  background: none;
  background-image: none;
  border: 1px solid white;
  color: white;
  &:focus {
    outline: 0;
    border-color: #1ab188;
  }
}
.form-submit {
  background: #1ab188;
  border: none;
  color: white;
  margin-top: 3rem;
  padding: 1rem 0;
  cursor: pointer;
  transition: background 0.2s;
  &:hover {
    background: #0b9185;
  }
}
</style>