<template>
  <div>
        <Menu></Menu>
    <v-container fluid>
      <v-layout wrap>
        <v-col cols="12" md="12">
          <v-card>
            <v-col class="text-center">
              <h1>Productos:</h1>

              <v-col offset="3" cols="6">
                <v-text-field
                  v-model="search"
                  append-icon="mdi-magnify"
                  label="Buscar producto"
                  single-line
                  hide-details
                ></v-text-field>
              </v-col>

              <v-btn color="#000000" dark @click="clickAddDialog"
                >Agregar producto</v-btn
              >
            </v-col>
            <v-col>
              <v-card-title>
                <v-col>
                  Listado de productos :
                  <v-spacer></v-spacer>
                </v-col>

                <p>Inversion total: ${{ totalCostos }}</p>
                <v-spacer></v-spacer>
              </v-card-title>
            </v-col>

            <v-data-table
              :headers="headers"
              :items="products"
              @click:row="clickElement"
              :items-per-page="100"
              item-key="id"
              :search="search"
              class="elevation-7 pointer"
            ></v-data-table>
          </v-card>

          <v-dialog v-model="dialogConfirm" width="600">
            <v-card>
              <v-card-title class="justify-center"
                >Agregar el producto?</v-card-title
              >
              <v-container>
                <div class="text-center pt-12">
                  <v-layout>
                    <v-flex>
                      <v-btn rounded color="#000000" dark @click="add"
                        >Agregar</v-btn
                      >
                    </v-flex>
                    <v-flex>
                      <v-btn
                        class=""
                        rounded
                        color="#000000"
                        dark
                        @click="dialogConfirm = false"
                        >Cancelar</v-btn
                      >
                    </v-flex>
                  </v-layout>
                </div>
              </v-container>
            </v-card>
          </v-dialog>

          <v-dialog v-model="dialogConfirmUpdate" width="600">
            <v-card>
              <v-card-title class="justify-center"
                >Editar el producto?</v-card-title
              >
              <v-container>
                <div class="text-center pt-12">
                  <v-layout>
                    <v-flex>
                      <v-btn rounded color="#000000" dark @click="update"
                        >Editar</v-btn
                      >
                    </v-flex>
                    <v-flex>
                      <v-btn
                        class=""
                        rounded
                        color="#000000"
                        dark
                        @click="dialogConfirmUpdate = false"
                        >Cancelar</v-btn
                      >
                    </v-flex>
                  </v-layout>
                </div>
              </v-container>
            </v-card>
          </v-dialog>

          <v-dialog v-model="dialogConfirmDestroy" width="600">
            <v-card>
              <v-card-title class="justify-center"
                >Eliminar el producto?</v-card-title
              >
              <v-container>
                <div class="text-center pt-12">
                  <v-layout>
                    <v-flex>
                      <v-btn rounded color="#000000" dark @click="destroy"
                        >Eliminar</v-btn
                      >
                    </v-flex>
                    <v-flex>
                      <v-btn
                        class=""
                        rounded
                        color="#000000"
                        dark
                        @click="dialogConfirmDestroy = false"
                        >Cancelar</v-btn
                      >
                    </v-flex>
                  </v-layout>
                </div>
              </v-container>
            </v-card>
          </v-dialog>

          <!--Dialog Agregar-->
          <v-dialog v-model="dialogAdd" width="600">
            <v-card>
              <v-card-title>Nuevo producto</v-card-title>
              <v-container>
                <v-row>
                  <v-col cols="12" md="12">
                    <v-select
                      v-model="providerSelected"
                      :items="providers"
                      label="Proveedor"
                      data-vv-name
                      item-value="id"
                      item-text="name"
                      required
                    ></v-select>
                    <v-text-field
                      v-model="productName"
                      label="Nombre del Producto"
                    ></v-text-field>
                    <v-text-field v-model="cost" label="Costo"></v-text-field>
                    <v-text-field v-model="price" label="Precio"></v-text-field>
                    <v-text-field v-model="stock" label="Stock"></v-text-field>
                    <v-text-field
                      v-model="critical"
                      label="Stock Crtico"
                    ></v-text-field>
                    <v-text-field
                      v-model="code"
                      label="Cdigo de Barras"
                    ></v-text-field>

                    <v-alert type="error" v-model="alert"
                      >Debe ingresar todos los campos y el precio debe ser mayor
                      al costo</v-alert
                    >
                  </v-col>
                  <v-divider></v-divider>

                  <v-flex xs12 offset-xs8>
                    <div class="text-center">
                      <v-btn
                        rounded
                        color="#000000"
                        dark
                        @click="addDialogConfirm"
                        >Agregar</v-btn
                      >
                    </div>
                  </v-flex>
                </v-row>
              </v-container>
            </v-card>
          </v-dialog>
          <!--FIN Dialog Agregar-->
          <!--Dialog-->
          <v-dialog v-model="dialog" width="600">
            <v-card>
              <v-card-title>Datos del producto</v-card-title>
              <v-container>
                <v-row>
                  <v-col cols="12" md="12">
                    <v-select
                      v-model="providerEdited"
                      :items="providers"
                      label="Proveedor"
                      data-vv-name
                      item-value="id"
                      item-text="name"
                      required
                    ></v-select>
                    <v-text-field
                      label="Nombre del Producto"
                      v-model="productNameEdited"
                    ></v-text-field>
                    <v-text-field
                      label="Costo"
                      v-model="costEdited"
                    ></v-text-field>
                    <v-text-field
                      label="Precio"
                      v-model="priceEdited"
                    ></v-text-field>
                    <v-text-field
                      label="Stock"
                      v-model="stockEdited"
                    ></v-text-field>
                    <v-text-field
                      label="Stock Crtico"
                      v-model="criticalEdited"
                    ></v-text-field>
                    <v-text-field
                      label="Cdigo de Barras"
                      v-model="codeEdited"
                    ></v-text-field>
                    <v-alert type="error" v-model="alertDialog"
                      >Debe ingresar todos los campos y el precio debe ser mayor
                      al costo para editar</v-alert
                    >
                  </v-col>
                  <v-divider></v-divider>

                  <v-flex xs12 offset-xs8>
                    <v-card-actions class="text-center">
                      <v-btn
                        color="#1195EB"
                        fab
                        small
                        dark
                        @click="addDialogConfirmUpdate"
                      >
                        <v-icon>mdi-pencil</v-icon>
                      </v-btn>

                      <v-btn color="#FF3333" fab small dark @click="addDialogConfirmDestroy">
                        <v-icon>mdi-delete</v-icon>
                      </v-btn>

                      <v-btn
                        color="primary"
                        fab
                        small
                        dark
                        @click="dialog = false"
                      >
                        <v-icon>mdi-exit-to-app</v-icon>
                      </v-btn>
                    </v-card-actions>
                  </v-flex>
                </v-row>
              </v-container>
            </v-card>
          </v-dialog>
          <!--FIN Dialog-->
        </v-col>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";
import Menu from "../components/Menu.vue";
export default {
    components: { Menu },
  data() {
    return {
      search: "",
      alert: false,
      dialogAdd: false,
      alertDialog: false,
      dialogConfirm: false,
      dialogConfirmUpdate: false,
      dialogConfirmDestroy: false,
      dialog: false,
      products: [],
      providers: [],
      productId: "",
      //agregar productos nuevos
      providerSelected: null,
      productName: "",
      cost: "",
      price: 0,
      stock: 0,
      critical: "",
      code: "",
      priceValidator: 0,
      //editar productos existentes
      providerEdited: null,
      productNameEdited: "",
      costEdited: "",
      priceEdited: "",
      stockEdited: "",
      criticalEdited: "",
      codeEdited: "",

      headers: [
        { text: "Proveedor", value: "provider.name" },
        { text: "Nombre", value: "name" },
        { text: "Codigo", value: "code" },
        { text: "Stock", value: "stock" },
        { text: "Critico", value: "critical" },
        { text: "Costo", value: "cost" },
        { text: "Precio", value: "price" },
      ],
    };
  },
  mounted: function () {
    this.getProducts();
    this.getProviders();
  },
  methods: {
    getProducts() {
      this.products = [];
      axios
        .get("http://127.0.0.1:8000/api/product/")
        .then((response) => (this.products = response.data));
    },

    getProviders() {
      axios
        .get("http://127.0.0.1:8000/api/provider/")
        .then((response) => (this.providers = response.data));
    },
    add(providerSelected) {
      this.dialogConfirm = false;
      this.priceValidator = this.price - this.cost;
      if (
        this.providerSelected != [] &&
        this.productName.length > 0 &&
        this.code > 0 &&
        this.stock > 0 &&
        this.cost > 0 &&
        this.price > 0 &&
        this.critical > 0 &&
        this.priceValidator > 0
      ) {
        this.alert = false;
        console.log(this.providerSelected);
        axios
          .post("http://127.0.0.1:8000/api/product/", {
            provider: this.providerSelected,
            name: this.productName,
            code: this.code,
            stock: parseFloat(this.stock),
            cost: parseInt(this.cost),
            price: parseInt(this.price),
            critical: parseFloat(this.critical),
          })
          .then(this.addActios());

        this.providerSelected = "";
        this.productName = "";
        this.code = "";
        this.stock = "";
        this.cost = 0;
        this.price = 0;
        this.critical = "";
      } else {
        this.alert = true;
      }
    },
    addActios() {
      this.dialogAdd = false;
      this.getProducts();
    },
    addDialogConfirm() {
      this.dialogConfirm = true;
    },
    addDialogConfirmUpdate() {
      this.dialogConfirmUpdate = true;
    },

    addDialogConfirmDestroy() {
      this.dialogConfirmDestroy = true;
    },
    update(providerEdited) {
      this.dialogConfirmUpdate = false;
      this.priceValidator = this.price - this.cost;
      console.log(this.priceValidator);

      console.log(this.providerEdited.id);
      this.alertDialog = false;
      axios
        .put("http://127.0.0.1:8000/api/product/" + this.productId, {
          provider: this.providerEdited.id,
          name: this.productNameEdited,
          code: this.codeEdited,
          stock: parseFloat(this.stockEdited),
          cost: parseInt(this.costEdited),
          price: parseInt(this.priceEdited),
          critical: parseFloat(this.criticalEdited),
        })
        .then(this.getProducts())
        .then((this.dialog = false));
    },

    destroy() {
      this.dialogConfirmDestroy = false;
      axios
        .delete("http://127.0.0.1:8000/api/product/" + this.productId)
        .then(this.getProducts())
        .then((this.dialog = false));
        
    },
    clickElement(value) {
      this.productId = value.id;
      this.providerEdited = value.provider;
      this.productNameEdited = value.name;
      this.costEdited = value.cost;
      this.priceEdited = value.price;
      this.stockEdited = value.stock;
      this.criticalEdited = value.critical;
      this.codeEdited = value.code;

      console.log(value.id);
      this.dialog = true;
    },
    clickAddDialog() {
      if (
        this.providerEdited != [] &&
        this.productNameEdited.length > 0 &&
        this.codeEdited > 0 &&
        this.stockEdited > 0 &&
        this.costEdited > 0 &&
        this.priceEdited > 0 &&
        this.criticalEdited > 0 &&
        this.priceValidator > 0
      ) {
      }

      this.dialogAdd = true;
      console.log(this.dialogAdd);
    },
  },
  computed: {
    totalCostos() {
      let total = 0;
      this.products.forEach((element) => {
        total = total + element.cost * element.stock;
      });

      return total;
    },
    //TODO: total de los productos
  },
};
</script>
<style>
.pointer {
  cursor: pointer;
}
</style>
