<template>
  <div>
        <Menu></Menu>
    <v-container>
      <v-row>
        <v-col cols="12" md="12">
          <v-card>
            <v-card-title>
              Proveedores
              <v-spacer></v-spacer>
              <v-btn color="#000000" dark @click="formDialog"
                >Agregar nuevo proveedor</v-btn
              >
              <v-spacer></v-spacer>
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Buscar proveedor"
                single-line
                hide-details
              ></v-text-field>
            </v-card-title>

            <v-data-table
              :headers="headers"
              :search="search"
              :items="providers"
              @click:row="clickElement"
              :items-per-page="100"
              item-key="id"
              class="elevation-7 pointer"
            ></v-data-table>
          </v-card>

          <!--Dialog agregar -->
          <v-row>
            <v-container>
              <v-dialog v-model="dialogAdd" width="600">
                <v-card>
                  <v-card-title>Agregar proveedor</v-card-title>

                  <v-col cols="12" md="12">
                    <v-text-field
                      label="Nombre del proveedor"
                      v-model="providerName"
                      v-on:keyup.enter="add"
                    ></v-text-field>

                    <v-alert type="error" v-model="alert"
                      >Debe ingresar todos los campos</v-alert
                    >
                    <v-flex xs12 offset-xs8>
                      <v-btn
                        rounded
                        color="#000000"
                        dark
                        @click="FormDialogConfirm"
                        >Agregar</v-btn
                      >
                    </v-flex>
                  </v-col>
                </v-card>
              </v-dialog>
            </v-container>
          </v-row>

          <!--Fin Dialog Agregar-->
          <v-dialog v-model="dialogConfirm" width="600">
            <v-card>
              <v-card-title class="justify-center"
                >Confirma la Edicion?</v-card-title
              >
              <v-container>
                <div class="text-center pt-12">
                  <v-layout>
                    <v-flex>
                      <v-btn rounded color="#000000" dark @click="add"
                        >Editar</v-btn
                      >
                    </v-flex>
                    <v-flex>
                      <v-btn
                        class=""
                        rounded
                        color="#000000"
                        dark
                        @click="dialogConfirm = false"
                        >Cancelar</v-btn
                      >
                    </v-flex>
                  </v-layout>
                </div>
              </v-container>
            </v-card>
          </v-dialog>

          <!--Dialog-->
          <v-dialog v-model="dialog" width="600">
            <v-card>
              <v-card-title>Datos del proveedor</v-card-title>
              <v-container>
                <v-row>
                  <v-col cols="12" md="12">
                    <v-text-field
                      label="Nombre"
                      v-model="providerAccion"
                    ></v-text-field>
                    <v-alert type="error" v-model="alertDialog"
                      >Debe ingresar todos los campos</v-alert
                    >
                  </v-col>
                  <v-divider></v-divider>

                  <v-flex xs12 offset-xs8>
                    <v-card-actions class="text-center">
                      <v-btn
                        color="#16D3DF"
                        fab
                        small
                        dark
                        @click="confirmDialogUpdate"
                      >
                        <v-icon>mdi-pencil</v-icon>
                      </v-btn>

                      <v-btn
                        color="red"
                        fab
                        small
                        dark
                        @click="confirmDialogDelete"
                      >
                        <v-icon>mdi-delete</v-icon>
                      </v-btn>

                      <v-btn
                        color="primary"
                        fab
                        small
                        dark
                        @click="dialog = false"
                      >
                        <v-icon>mdi-exit-to-app</v-icon>
                      </v-btn>
                    </v-card-actions>
                  </v-flex>
                </v-row>
              </v-container>
            </v-card>
          </v-dialog>
          <!--FIN Dialog-->

          <v-dialog v-model="dialogUpdate" width="600">
            <v-card>
              <v-card-title class="justify-center"
                >Seguro que desea editar?</v-card-title
              >
              <v-container>
                <div class="text-center pt-12">
                  <v-layout>
                    <v-flex>
                      <v-btn rounded color="#000000" dark @click="update"
                        >si</v-btn
                      >
                    </v-flex>
                    <v-flex>
                      <v-btn
                        class=""
                        rounded
                        color="#000000"
                        dark
                        @click="dialogUpdate = false"
                        >no</v-btn
                      >
                    </v-flex>
                  </v-layout>
                </div>
              </v-container>
            </v-card>
          </v-dialog>

          <v-dialog v-model="dialogDelete" width="600">
            <v-card>
              <v-card-title class="justify-center"
                >Seguro que desea eliminar?</v-card-title
              >
              <v-container>
                <div class="text-center pt-12">
                  <v-layout>
                    <v-flex>
                      <v-btn rounded color="#000000" dark @click="destroy"
                        >si</v-btn
                      >
                    </v-flex>
                    <v-flex>
                      <v-btn
                        class=""
                        rounded
                        color="#000000"
                        dark
                        @click="dialogAdd = false"
                        >no</v-btn
                      >
                    </v-flex>
                  </v-layout>
                </div>
              </v-container>
            </v-card>
          </v-dialog>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>


<script>
import axios from "axios";
import Menu from "../components/Menu.vue";
export default {
    components: { Menu },
  data() {
    return {
      search: "",
      dialog: false,
      dialogAdd: false,
      alert: false,
      dialogConfirm: false,
      alertDialog: false,
      dialogDelete: false,
      dialogUpdate: false,
      providerConfirmation: 0,
      providers: [],
      providerName: "",
      providerAccion: "",
      providerId: "",
      headers: [
        {
          text: "ID",
          align: "left",
          value: "id",
        },
        { text: "Nombre", value: "name" },
      ],
    };
  },
  mounted: function () {
    this.getData();
  },
  methods: {
    getData() {
      this.providers = [];
      axios
        .get("http://127.0.0.1:8000/api/provider/")
        .then((response) => (this.providers = response.data));
    },
    FormDialogConfirm() {
      if (this.providerName.length <= 0) {
        this.alert = true;
      } else {
        this.alert = false;
        this.dialogConfirm = true;
      }
    },
    confirmDialogAdd() {
      this.dialogAdd = true;
    },
    confirmDialogUpdate() {
      this.dialogUpdate = true;
    },
    confirmDialogDelete() {
      this.dialogDelete = true;
    },
    add() {
      if (this.providerName.length > 0) {
        this.alert = false;

        axios
          .post("http://127.0.0.1:8000/api/provider/", {
            name: this.providerName,
          })
          .then(this.addActions());
        this.providerName = "";
        this.dialogConfirm = false;
      } else {
        this.alert = true;
        this.dialogConfirm = false;
      }
    },
    addActions() {
      this.getData();
      this.dialogAdd = false;
    },
    update() {
      this.alertDialog = false;
      this.dialogUpdate = false;
      if (this.providerAccion.length > 0) {
        axios
          .put("http://127.0.0.1:8000/api/provider/" + this.providerId, {
            name: this.providerAccion,
          })
          .then(this.getData())
          .then((this.dialog = false));
      } else {
        this.alertDialog = true;
      }
    },

    destroy() {
      this.dialogDelete = false;
      axios
        .delete("http://127.0.0.1:8000/api/provider/" + this.providerId)
        .then((response) => (this.providerConfirmation = response.data))
        .then(this.actionsDestroy());
        console.log(this.providerConfirmation)
    },
    actionsDestroy() {
      this.getData();
      this.dialog = false;
    },

    clickElement(value) {
      this.providerAccion = value.name;
      this.providerId = value.id;
      console.log(value.name);
      this.dialog = true;
    },
    formDialog() {
      this.dialogAdd = true;
    },
  },
};
</script>
<style>
.pointer {
  cursor: pointer;
}
</style>

