<template>
  <div>
        <Menu></Menu>
    <v-container>
      <v-row>
        
        <v-col cols="12" md="12" v-if="allProducts">
          <v-card>
            <v-card-title>
              Stock Crtico
              <v-spacer></v-spacer>
              <v-select
                v-model="providerSelected"
                :items="providers"
                label="Proveedor"
                data-vv-name
                item-value="id"
                item-text="name"
                required
                @input="getByProvider"
              ></v-select>
              
            </v-card-title>
            <v-data-table
              :headers="headers"
              :items="allCritics"
              :items-per-page="100"
              item-key="id"
              class="elevation-7"
            ></v-data-table>
          </v-card>
        </v-col>
        <v-col cols="12" md="12" v-if="byProvider">
          <v-card> 
            <v-card-title>
              Stock Crtico por Proveedor
              <v-spacer></v-spacer>
              <v-select
                v-model="providerSelected"
                :items="providers"
                label="Proveedor"
                data-vv-name
                item-value="id"
                item-text="name"
                required
                @input="getByProvider"
              ></v-select>
            </v-card-title>
          <v-data-table
            :headers="headersProv"
            :items="criticsByProviders"
            :items-per-page="100"
            item-key="id"
            class="elevation-7"
          ></v-data-table>
          </v-card> 
          
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";
import Menu from "../components/Menu.vue";
export default {
    components: { Menu },
  data() {
    return {
      providers: [],
      allCritics: [],
      criticsByProviders: [],
      allProducts: true,
      byProvider: false,
      providerSelected: null,
      headers: [
        { text: "Producto", value: "name" },
        { text: "Costo", value: "cost" },
        { text: "Precio", value: "price" },
        { text: "Stock", value: "stock" },
        { text: "Critico", value: "critical" }
      ],
      headersProv: [
        { text: "Producto", value: "name" },
        { text: "Costo", value: "cost" },
        { text: "Precio", value: "price" },
        { text: "Stock", value: "stock" },
        { text: "Critico", value: "critical" }
      ]
    };
  },
  mounted: function() {
    this.getProviders();
    this.getAllCritics();
    console.log(this.providerSelected);
    this.getByProvider();
  },
  methods: {
    getProviders() {
      axios
        .get("http://127.0.0.1:8000/api/provider/")
        .then(response => (this.providers = response.data));
    },
    getAllCritics() {
      axios
        .get("http://127.0.0.1:8000/api/critical/")
        .then(response => (this.allCritics = response.data));
    },
    getByProvider(providerSelected) {
      console.log(this.providerSelected);
      if (this.providerSelected != null) {
        this.allProducts = false;
        this.byProvider = true;
        axios
          .get("http://127.0.0.1:8000/api/critical/" + this.providerSelected)
          .then(response => (this.criticsByProviders = response.data));
        console.log(this.providerSelected);
      }
    }
  }
};
</script>